const NOT_PROD = process.env.NODE_ENV !== 'production';
export const logger = {
    info(msg, options) {
        if (NOT_PROD)
            console.info(msg);
    },
    warn(msg, options) {
        if (NOT_PROD)
            console.warn(msg);
    },
    warnOnce(msg, options) {
        if (NOT_PROD)
            console.warn(msg);
    },
    error(msg, options) {
        if (NOT_PROD)
            console.error(msg);
    },
    clearScreen(type) {
        if (NOT_PROD)
            console.clear();
    },
    // eslint-disable-next-line @typescript-eslint/no-redundant-type-constituents
    hasErrorLogged(error) {
        if (NOT_PROD)
            console.error(error);
    },
    hasWarned: false,
};
