export const RequestMethod = {
    GET: 'GET',
    QUERY: 'QUERY',
    HEAD: 'HEAD',
    POST: 'POST',
    PUT: 'PUT',
    DELETE: 'DELETE',
    OPTIONS: 'OPTIONS',
    PATCH: 'PATCH',
};
// TODO: put in engine
export class RouteModule {
    #staticPaths;
    get staticPaths() {
        return this.#staticPaths;
    }
    #locals;
    get locals() {
        return this.#locals;
    }
    #handler;
    get handler() {
        return this.#handler;
    }
    #document;
    get document() {
        return this.#document;
    }
    #prerender;
    get prerender() {
        return this.#prerender;
    }
    #template;
    get template() {
        return this.#template;
    }
    constructor(options) {
        if (typeof options.staticPaths === 'function')
            this.#staticPaths = options.staticPaths;
        if ((typeof options.handler === 'object' ||
            typeof options.handler === 'function') &&
            options.handler)
            this.#handler = options.handler;
        this.#locals = options.locals;
        if (typeof options.template === 'function')
            this.#template = options.template;
        if (typeof options.document === 'function')
            this.#document = options.document;
        if (typeof options.prerender === 'boolean')
            this.#prerender = options.prerender;
    }
}
